.modal-body {
    @include null-margins-paddings();
    @include clearfix();
    padding-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

    .modal-body_left-selector,
    .modal-body_right-selector {
        width: 36%;
    }

        .sleshselect-title {
            display: block;
            color: $c-black-light;
            padding-bottom: 5px;
        }

        .slushselect {
            width: 100%;
        }

    .modal-body_move-buttons {
        width: 8%;
        display: flex;
        flex-direction: column;
        padding: 5px;
    }

    .__left {
        transform: rotate(90deg);
    }

    .__right {
        transform: rotate(-90deg);
    }

    .__top {
        transform: rotate(180deg);
    }
