@import "../config/globals";

.modal-body_move-w {
    width: 100%;
    height: 40px;
    margin: auto;

    background: none;
    border: 1px solid $border-color;
    border-radius: 4px;
    padding: 0;

    float: right;

    position: relative;
    opacity: 1;
}

.modal-body_move-w:hover,
.modal-body_move-w:focus {
    filter: brightness(0);
}

    .modal-body_move_icon {
        width: 100%;
        height: 24px;

        background: url('../../images/icons/select-all.png') no-repeat 50% 50%;
        position: absolute;
        left: 50%;
        top: 50%;

        //margin: -12px -18px;
        margin: -12px -50%;
        opacity: 2;
    }

    .modal-body_move {

        background: none;
        border: none;
        padding: 0;
        margin: 0;
        width: 100%;
        height: 40px;

        cursor: pointer;
        position: absolute;
        opacity: -1;
    }
